<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-lx-cascades"></i> 
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-button
          type="primary"
          icon="el-icon-delete"
          class="handle-del mr10"
          @click="delAllSelection"
        ></el-button>
        <el-select v-model="query.address" placeholder="" class="handle-select mr10">
          <el-option key="1" label="" value=""></el-option>
          <el-option key="2" label="" value=""></el-option>
        </el-select>
        <el-input v-model="query.name" placeholder="" class="handle-input mr10"></el-input>
        <el-button type="primary" icon="el-icon-search" @click="handleSearch"></el-button>
      </div>
      <el-table
        :data="tableData"
        border
        class="table"
        ref="multipleTable"
        header-cell-class-name="table-header"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55" align="center"></el-table-column>
        <el-table-column prop="id" label="ID" width="55" align="center"></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">{{scope.row.money}}</template>
        </el-table-column>
        <el-table-column label="()" align="center">
          <template slot-scope="scope">
            <el-image
              class="table-td-thumb"
              :src="scope.row.thumb"
              :preview-src-list="[scope.row.thumb]"
            ></el-image>
          </template>
        </el-table-column>
        <el-table-column prop="address" label=""></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.state===''?'success':(scope.row.state===''?'danger':'')"
            >{{scope.row.state}}</el-tag>
          </template>
        </el-table-column>

        <el-table-column prop="date" label=""></el-table-column>
        <el-table-column label="" width="180" align="center">
          <template slot-scope="scope">
            <el-button
              type="text"
              icon="el-icon-edit"
              @click="handleEdit(scope.$index, scope.row)"
            ></el-button>
            <el-button
              type="text"
              icon="el-icon-delete"
              class="red"
              @click="handleDelete(scope.$index, scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          background
          layout="total, prev, pager, next"
          :current-page="query.pageIndex"
          :page-size="query.pageSize"
          :total="pageTotal"
          @current-change="handlePageChange"
        ></el-pagination>
      </div>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="editVisible" width="30%">
      <el-form ref="form" :model="form" label-width="70px">
        <el-form-item label="">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.address"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false"> </el-button>
                <el-button type="primary" @click="saveEdit"> </el-button>
            </span>
    </el-dialog>
  </div>
</template>

<script>
  import { fetchData } from '../../api/index';
    export default {
        name: "BaseTable",
      data() {
        return {
          query: {
            address: '',
            name: '',
            pageIndex: 1,
            pageSize: 10
          },
          tableData: [],
          multipleSelection: [],
          delList: [],
          editVisible: false,
          pageTotal: 0,
          form: {},
          idx: -1,
          id: -1
        };
      },
      created() {
        this.getData();
      },
      methods: {
        //  easy-mock 
        getData() {
          fetchData(this.query).then(res => {
            console.log(res);
            this.tableData = res.list;
            this.pageTotal = res.pageTotal || 50;
          });
        },
        // 
        handleSearch() {
          this.$set(this.query, 'pageIndex', 1);
          this.getData();
        },
        // 
        handleDelete(index, row) {
          // 
          this.$confirm('', '', {
            type: 'warning'
          })
            .then(() => {
              this.$message.success('');
              this.tableData.splice(index, 1);
            })
            .catch(() => {});
        },
        // 
        handleSelectionChange(val) {
          this.multipleSelection = val;
        },
        delAllSelection() {
          const length = this.multipleSelection.length;
          let str = '';
          this.delList = this.delList.concat(this.multipleSelection);
          for (let i = 0; i < length; i++) {
            str += this.multipleSelection[i].name + ' ';
          }
          this.$message.error(`${str}`);
          this.multipleSelection = [];
        },
        // 
        handleEdit(index, row) {
          this.idx = index;
          this.form = row;
          this.editVisible = true;
        },
        // 
        saveEdit() {
          this.editVisible = false;
          this.$message.success(` ${this.idx + 1} `);
          this.$set(this.tableData, this.idx, this.form);
        },
        // 
        handlePageChange(val) {
          this.$set(this.query, 'pageIndex', val);
          this.getData();
        }
      }
    };
</script>

<style scoped>
  .handle-box {
    margin-bottom: 20px;
  }
  .handle-select {
    width: 120px;
  }
  .handle-input {
    width: 300px;
    display: inline-block;
  }
  .table {
    width: 100%;
    font-size: 14px;
  }
  .red {
    color: #ff0000;
  }
  .mr10 {
    margin-right: 10px;
  }
  .table-td-thumb {
    display: block;
    margin: auto;
    width: 40px;
    height: 40px;
  }
</style>



